@import "@/styles/_variables.scss";@import "@/styles/_mixins.scss";@import "@/styles/_fonts.scss";@import "@/styles/_reset.scss";@import "@/styles/_global.scss";
.layout {
  .top,
  .bottom {
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
  }
}

.experience {
  background-color: $nightblue;
  color :$whitepink;
  display: grid;

  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 1fr);

  @include rem-fallback(grid-gap, 5px );
  
  .expyear {
    display: inline-flex;
    justify-self: center;
    font-family: 'Anton', sans-serif;
    font-weight: 800;
    position: relative;
    line-height: 1;

    @include rem-fallback(font-size, (16px * 6) );
    @include rem-fallback(letter-spacing, 5px );

    &::before,
    &::after{
      position: absolute;
      @include rem-fallback(right, (16px * -3) );
      bottom: 0;
    }
    &::before{
      content: '+';
      /* bottom: auto; */
      color: $fakered;
      right: auto;

      @include rem-fallback(font-size, (16px * 4) );
      @include rem-fallback(left, (16px * -3) );
      /* @include rem-fallback(top, (16px / -1) ); */
      
    }
    &::after {
      content: 'y';
      @include rem-fallback(font-size, (16px * 4) );
    }
  }
}

@include for-size(desktop-up) {
  .experience {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
  }
}

.details {
  display: inline-block;
  .domains,
  .more-details {
    display: block;

    .list {
      font-family: 'Open Sans', sans-serif;
      font-weight: 300;
      color : rgba(156,157,161,1);
      text-align: left;
      position: relative;
      display: inline-block;

      @include rem-fallback(font-size, 16px );
      @include rem-fallback(letter-spacing, 3px );

      li {
        @include rem-fallback(margin-left, 16px );
        @include rem-fallback(margin-bottom, 8px );

        &::first-letter {
          text-transform: uppercase;
        }
      }
      &.tags {
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: space-evenly;

        @include rem-fallback(font-size, 12px );
        @include rem-fallback(letter-spacing, 1px );
        @include rem-fallback(margin-top, 8px );
        @include rem-fallback(margin-bottom, 16px*2 );

        li {
          background-color: $nightblue;
          color: $whitepink;
          display: inline-block;
          font-weight: 500;
          white-space: nowrap;

          @include rem-fallback(margin-left, 0 );
          @include rem-fallback(border-radius, 10px );
          @include rem-fallback(padding, 3px, 10px );

        }
      }
      &:not(.tags) {
        &:before {
          content: '';

          height: 105%;
          position: absolute;
          background-color: red;
          top : 50%;
          left: 0;
          transform: translate3d(0, -50%, 0);

          @include rem-fallback(width, 1px );
        }
      }

    }
  }

  .pushline {
    display: grid;
    text-transform: uppercase;

    @include rem-fallback(margin-bottom, 32px );
    @include rem-fallback(font-size, 16px ); 
    
    em {
      color: $fakered;
      font-weight: 800;
      font-family: 'Quicksand', sans-serif;

      @include rem-fallback(font-size, (16px *2) ); 
    }
  }
}

@include for-size(desktop-up) {
  .details {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    justify-content: center;
    align-items: center;

    .domains,
    .more-details {
      display: inline-flex;
      align-items: center;
      justify-content: space-around;

      .list {

        li {
          @include rem-fallback(font-size, (16px *1.5) );
        }

        &.tags {
          display: block;
          li {
            display: block;
            background-color: transparent;
            font-family: 'Open Sans', sans-serif;
            font-weight: 300;
            color : rgba(156,157,161,1);
            border-radius: none;
            padding: auto;

            @include rem-fallback(margin-left, 16px/2 );
          }

          &:before {
            content: '';
            height: 105%;
            position: absolute;
            background-color: red;
            top : 50%;
            left: 0;
            transform: translate3d(0, -50%, 0);

            @include rem-fallback(width, 1px );
          }
        }

      }
    }
    .pushline {
      display: block;
    }
  }
}